import { menuManager, showDataByMenu } from "./lib_cv/menus/index.js";

window.addEventListener("load", function(){
    // const ul = document.getElementById("menu");
    // const menuDefaultActive = ul.querySelector("li:first-child"); 

    // const mobile = window.matchMedia("(max-width: 1000px)")
    // const contentElement = document.querySelector(".content");
    // const infosPersonnals  = document.querySelector(".mobil");
    // const desktop = document.querySelector(".desktop");
    
    // if(mobile.matches) {
    //     menuDefaultActive.classList.add("active-top", "active");
    //     smScreen_toColumns(document.querySelector(".profil"));
    //     desktop.remove();
    // }else{
    //     menuDefaultActive.classList.add("active-bottom", "active");
    //     infosPersonnals.remove();
    // } 

    // memuManager(ul, DATA);

    // window.addEventListener("resize", function() {
    //     if(mobile.matches){
    //         smScreen_toColumns(document.querySelector(".profil"))
    //         contentElement.classList.remove("vh-100");
    //     }else {
    //         lgScreen_toRows(document.querySelector(".profil"));
    //         contentElement.classList.add("vh-100");
    //     }
    // });   

    // window.onscroll = function() {toggleBottomMenuMobile_smScreen(document.querySelector("header"), contentElement)};

    /////////////////////////////////////////////////////////////////////
    const mobile = window.matchMedia("(max-width: 1000px)")
    const globalContainer = document.querySelector("[mobile-app]");
    globalContainer.classList.add("vh-100", "d-flex", "flex-column");
    const nav = document.querySelector("nav");
    
    if(mobile.matches){
        document.querySelectorAll("[ma-hidden]").forEach(element => {
            element.remove();
        });
        //globalContainer.classList.remove("vh-100");
        nav.classList.add("bg-light");
        nav.style.zIndex = 10000;
        document.querySelector("[ma-body]").style.marginBottom = "3rem";

        headerToProfil(); 
        bottomtabNavigation(globalContainer);
    } else {
        const header = document.querySelector("header");
       
        const main = document.querySelector("main");
        document.querySelector("[ma-ellipsis]").remove();
        header.classList.add("container", "text-center","d-flex", "flex-column", "align-items-center")

        nav.classList.add("container", "d-flex", "justify-content-between", "border-bottom", "bg-white", "mt-3", "pt-2");
        nav.querySelector("ul").classList.add("flex-fill");
        nav.querySelectorAll("li").forEach(element => {
            element.classList.add("nav-link", "text-dark", "me-2");
            element.firstElementChild.classList.add("icon-color-primary");
            element.lastElementChild.classList.add("ms-2");
        });
        main.classList.add("container", "bg-white", "flex-fill");
        document.querySelector("[ma-body]").classList.add("mt-4");
    }

    menuManager(DATA);

    const lang = document.querySelector("#lang-choice");
    const iconLang = document.querySelector("div#icon-lang");

    lang.addEventListener("change", event => {
        const menuActive = document.querySelector("[active]")
        showDataByMenu(menuActive, DATA[0], lang.value);

        const translates = document.querySelectorAll("[translate]");
        lang.value === "fr" ? iconLang.classList.replace("icon-en", "icon-fr") : iconLang.classList.replace("icon-fr", "icon-en");
        translates.forEach(element => {
            const translate = element.getAttribute("translate");
            element.setAttribute("translate", element.textContent);
            element.innerText = translate;
        });
    });
})
