const bottomtab = document.querySelector("[ma-bottom-tabs]");

export const bottomtabNavigation = (globalContainer) => {
    globalContainer.classList.add("position-relative", "bg-light");
    bottomtab.classList.add("position-fixed", "bottom-0", "border-top", "w-100");
    bottomtab.querySelector("ul").classList.add("w-100");
    const menuItems = bottomtab.querySelectorAll("li");
    console.log(menuItems);
    menuItems.forEach(item => {
        item.classList.add("d-flex", "flex-column", "flex-fill", "align-items-center");
    });
}