const createLiWithContent = (content) => {
    const li = document.createElement("li");
    li.classList.add("list-group-item");
    li.innerHTML = content;
    return li;
}

export const createDivWithClasses = (classes) => {
    const div = document.createElement("div");
    classes.forEach(element => {
        div.classList.add(element);
    });
    return div;
}
const createUl = () => {
    const ul = document.createElement("ul");
    ul.classList.add("list-group", "list-group-flush");

    return ul;
}
export const createCard = (title, headerIcon,  dataListe) => {
    const card = createDivWithClasses(["card"]);
    const headerCard = createDivWithClasses(["card-header", "bg-primary-custum"]);
    headerCard.innerHTML = headerIcon+title;

    const list = createUl();
    dataListe.forEach(data => {
        list.append(createLiWithContent(data));
    })

    card.append(headerCard, list);
    return {"card": card, "hearderCard": headerCard, "list": list};
}