const header = document.querySelector("[ma-profil-header]");

export const headerToProfil = () => {
    header.classList.add("d-flex", "flex-column", "align-items-center");
    const name = header.querySelector("[ma-profil-name]");
    name.classList.add("text-center", "mt-2");

    const ellipsis = header.querySelector("[ma-ellipsis]");
    if(ellipsis !== null){
        header.classList.add("position-relative");
        ellipsis.classList.add("position-absolute", "top-0", "end-0", "me-3", "mt-3");
    }
}