const body = document.querySelector("[ma-body]");

const formations = (block, data) => {
    const div = document.createElement("div");
    div.classList.add("row", "border-bottom", "mx-3", "mt-4");

    const divCol1 = document.createElement("div");
    divCol1.classList.add("col-sm-4");
    const year = document.createElement("span");
    year.innerHTML = data.year;

    divCol1.append(year);

    const divCol2 = document.createElement("div");
    divCol2.classList.add("col-sm-8");
    const degree = document.createElement("span");
    degree.classList.add("h6");
    degree.innerHTML = data.degree;

    const org = document.createElement("span");
    org.innerHTML = data.organisation+', ';

    const city = document.createElement("span");
    city.innerHTML = data.city+`<br>`;

    const mention = document.createElement("span");
    mention.innerHTML = "mention : "+data.mention;

    divCol2.append(degree, org, city, mention);

    div.append(divCol1, divCol2);
    block.append(div);
}

export const makeFormation = (lang, data) => {
    const header = document.createElement("h2");
    header.classList.add("card-header", "bg-primary-custum");

    const i = document.createElement("i");
    i.classList.add("fas", "fa-graduation-cap","icon-color-second", "me-2");
    const span = document.createElement("span");
    span.setAttribute("translate", `${lang !== 'fr' ? "Formations":"Formations"}`)
    span.innerHTML = "Formations";
    header.append(i, span);

    body.append(header);
    data[lang].forEach(element => {
       formations(body, element);
    });
}