import { makeProfil } from "../profil/index.js";
import { makeExperience } from "../experiences/index.js";
import { makeFormation } from "../formations/index.js";
import { makeCompetences } from "../competences/index.js";

const menus = document.querySelectorAll("[menu]");
const defaultMenuSelected = document.querySelector("[default-active]");
const nav = document.querySelector("nav");
const body = document.querySelector("[ma-body]");
const lang = document.querySelector("#lang-choice");

/////////////////////////////////////////////////////////////////////////
const callBackMenuStandard = {
    "profil": makeProfil,
    "experiences": makeExperience,
    "formations": makeFormation,
    "skills": makeCompetences
};
////////////////////////////////////////////////////////////////////////

const mobile = window.matchMedia("(max-width: 1000px)");

const activeMenu = (menu) => {
    if(mobile.matches){
        menu.classList.add("active-top", "menu-click");
    } else {
        menu.classList.add("active", "menu-click");
    }
    menu.setAttribute("active", "");
}
const menuDesactive = (menu) => {
    if(mobile.matches){
        menu.classList.remove("active-top", "menu-click");
    } else {
        menu.classList.remove("active", "menu-click");
    }
    menu.removeAttribute("active");
}

export const showDataByMenu = (menuActive, data, lang) => {
    while (body.firstChild) {
        body.removeChild(body.firstChild);
    }
    const menuValue = menuActive.getAttribute("menu"); 
    callBackMenuStandard[menuValue](lang, data[menuValue])
}

export const menuManager = (data) => {
    const cvData = data[0];
    if(defaultMenuSelected){
        activeMenu(defaultMenuSelected);
        showDataByMenu(defaultMenuSelected, cvData, lang.value);
    }
    menus.forEach(menu => {
        menu.addEventListener("click", function(event) {
            for (let index = 0; index < menus.length; index++) {
                if(menus[index] !== this && menus[index].hasAttribute("active")) {
                    menuDesactive(menus[index]);
                    break;
                 }   
            }
           activeMenu(this);
           showDataByMenu(this, cvData, lang.value);
        });
    });
}