import { createDivWithClasses, createCard } from "./factories.js";
const body = document.querySelector("[ma-body]");

export const makeProfil = (lang, data) => {
    const h2 = document.createElement("h2");
    h2.setAttribute("translate", `${lang!=='fr'? "Presentation": "Présentation"}`);
    h2.innerText = "Présentation";

    const p = document.createElement("p");
    p.innerText = data.presentation[lang];


    //cards
    const row1 = createDivWithClasses(["row"]);
    const col61Row1 = createDivWithClasses(["col-sm-6"]);
    col61Row1.append(createCard(`<span translate="${lang!=='fr'? 'Address':'Adresse'}">Adresse<span>`,
     `<i class="fas fa-map-marker-alt me-2 icon-color-second"></i>`, [
        data.address.street, data.address.city+", "+data.address.post_code
    ]).card);
    const col62Row1 = createDivWithClasses(["col-sm-6"]);
    col62Row1.append(createCard(`<span translate="${lang!=='fr'? 'Additional information':'Informations supplémentaires'}">Informations supplémentaires</span>`,
     `<i class="fas fa-info-circle me-2 icon-color-second"></i> `, [
        data.drivers_licence[lang], data.mobility[lang]
    ]).card);
    row1.append(col61Row1, col62Row1);

    const row2 = createDivWithClasses(["row", "mt-3"]);
    const col61Row2 = createDivWithClasses(["col-sm-6"]);
    col61Row2.append(createCard(`<span translate='Contacts'>Contacts</span>`,
     `<i class="fas fa-address-card me-2 icon-color-second"></i>`, [
        data.contacts.telephone, `<a href="${data.contacts.linkedIn}">LinkedIn</a>`,
        `<a href=mailto:marouanadiallo1@gmail.com>${data.contacts.email}</a>`
    ]).card)
    const col62Row2 = createDivWithClasses(["col-sm-6"]);
    col62Row2.append(createCard(`<span translate="${lang!=='fr' ? 'Languages':'Langues'}">Langues</span>`,
     `<i class="fas fa-globe me-2 icon-color-second"></i>`, 
        data.languages[lang].map(element => {
            return `
                <div class="d-flex align-items-center">
                    <span>${element.lang} </span>
                    <div class="progress flex-fill ms-3">
                        <div class="progress-bar bg-gris-custum w-${element.rate}" role="progressbar" aria-valuenow="${element.rate}" aria-valuemin="0" aria-valuemax="100">${element.rate}%</div>
                    </div>
                </div>
            `
        })
    ).card)
    row2.append(col61Row2,col62Row2);
    // obbies

    //add to body
    body.append(h2, p, row1, row2);
}