const body = document.querySelector("[ma-body]");

const headerExp = (organisation, year) => {
    const div = document.createElement("div");
    div.classList.add("row", "card-header", "mx-1", "my-2");

    const divCol1 = document.createElement("div");
    divCol1.classList.add("col-sm-10", "h5");
    divCol1.innerText = organisation;

    const divCol2 = document.createElement("div");
    divCol2.classList.add("col-sm-2");
    divCol2.innerHTML = `<span>${year}</span>`;
    
    div.append(divCol1, divCol2);
    return div;
}
const inlineP  = (title, projet) => {
    const p = document.createElement("p");
    p.classList.add("mx-2");
    p.innerHTML = `<span class="me-2 h6">${title} : </span> ${projet}`;

    return p;
}

const realisations = (title, liste) => {
    const div = document.createElement("div");
    div.classList.add("mx-2");
    const p = document.createElement("p");
    p.classList.add("h6");
    p.innerHTML = title;

    const ul = document.createElement("ul");

    liste.forEach(value => {
        const li = document.createElement("li");
        li.classList.add("realisation-circle");
        li.innerHTML = `<span class="ms-1">${value}</span>`;
        ul.append(li)
    });
    div.append(p, ul);

    return div;
} 

const environements = (title, liste) => {
    const div = document.createElement("div");
    div.classList.add("mx-2");

    const p = document.createElement("p");
    p.classList.add("h6");
    p.innerHTML = title;

    const pContent = document.createElement("p");

    let content ="";
    liste.forEach(value => {
      content += value + ", "
    });
    content = content.slice(0, -2);
    pContent.innerHTML = content+".";
    div.append(p, pContent);

    return div;
}

const experience = (body, data) => {
    body.append(
        headerExp(data.organisation, data.year),
        inlineP("Projet", data.projet),
        inlineP("Rôle", data.role),
        realisations("Réalisations :", data.realisation),
        environements("Environnement technique :", data.environnement)
    );
}

export const makeExperience = (lang, data) => {
    const header = document.createElement("h2");
    header.classList.add("card-header", "bg-primary-custum");

    const i = document.createElement("i");
    i.classList.add("fas", "fa-briefcase","icon-color-second", "me-2");
    const span = document.createElement("span");
    span.setAttribute("translate", `${lang !== 'fr' ? "Professional experience":"Expériences professionnelles"}`)
    span.innerHTML = "Expériences professionnelles";
    header.append(i, span);

    //console.log(data[lang]);
    body.append(header);
    data[lang].forEach(element => {
       experience(body, element);
    });

}