const body = document.querySelector("[ma-body]");

const competence = (parent, data) =>{
    const div = document.createElement("div");

    data.forEach(da => {
        const container = document.createElement("div");
        container.classList.add("row", "mx-1", "my-3", "border-bottom");

        const col1 = document.createElement("div");
        col1.classList.add("col-sm-6", "px-0");
        col1.innerHTML = da.name;

        const col2 = document.createElement("div");
        col2.classList.add("col-sm-6", "px-0");
        col2.innerHTML = `
            <div class="progress">
                <div class="progress-bar bg-gris-custum" role="progressbar" style="width: ${da.rate}%;" aria-valuenow="${da.rate}" aria-valuemin="0" aria-valuemax="100">${da.rate}%</div>
            </div>
        `;

        container.append(col1, col2);
        parent.append(container);
    });
}

export const makeCompetences = (lang, data) => {
    const header = document.createElement("h2");
    header.classList.add("card-header", "bg-primary-custum");

    const i = document.createElement("i");
    i.classList.add("fas", "fa-graduation-cap", "icon-color-second", "me-2");
    const span = document.createElement("span");
    span.setAttribute("translate", `${lang !== 'fr' ? "Competences":"Compétences"}`)
    span.innerHTML = "Compétences";
    header.append(i, span);

    body.append(header);
    data[lang].forEach(element => {
       console.log(element);
       const div = document.createElement("div");
       div.classList.add("mx-2");

       const header = document.createElement("div");
       header.classList.add("card-header", "mt-2", "h5");
       header.innerHTML = element.domain;


       div.append(header);
       competence(div, element.languages);
       body.append(div);
    });
}